<template>
  <div>
    <div class="basic-info-box">
      <el-row>
        <el-col :span="8" v-for="(item, index) of configList" :key="index">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">{{ item.label }}</div>
            <div class="basic-info-card__value">{{ item.value }}</div>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8">
          <div class="basic-info-card">
            <div class="basic-info-card__label bg-purple">Failure Description</div>
            <div class="basic-info-card__value">-</div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  name: 'BasicInfoItem',

  props: {
    basicInfo: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      propsOrder: [
        {
          name: 'receiptNo',
          label: this.$t('IDC.accountStatement.receiptNo'),
        },
        {
          name: 'accountType',
          label: this.$t('IDC.accountStatement.status'),
        },
        {
          name: 'alias',
          label: this.$t('IDC.accountStatement.transactionType'),
        },
        {
          name: 'currency',
          label: this.$t('IDC.accountStatement.reasonType'),
        },
        {
          name: 'accountRelationship',
          label: this.$t('IDC.accountStatement.initiationTime'),
        },
        {
          name: 'currentBalance',
          label: this.$t('IDC.accountStatement.lastUpdateTime'),
        },
        {
          name: 'availableBalace',
          label: this.$t('IDC.accountStatement.initiator'),
        },
        {
          name: 'reservedBalance',
          label: this.$t('IDC.accountStatement.initiatorType'),
        },
        {
          name: 'unclearBalance',
          label: this.$t('IDC.accountStatement.amount'),
        },
        {
          name: 'status',
          label: this.$t('IDC.accountStatement.channel'),
        },
      ],
    };
  },

  computed: {
    configList() {
      const configMap = this.basicInfo;
      return this.propsOrder.map((field) => {
        const name = field.name;
        const label = field.label;
        return {
          label,
          value: configMap[name] || '-',
        };
      });
    },
  },
};
</script>
<style scoped lang="scss">
.basic-info-box {
  font-weight: bold;
  font-size: 16px;
  padding: 20px;
}
.el-row {
  .basic-info-card {
    padding-bottom: 20px;
    font-size: 14px;

    &__label {
      margin-bottom: 8px;
      color: $--text_color--secondary;
    }

    &__value {
      color: $--text_color--strong;
    }
  }
}
</style>
